import React, { Component } from 'react';

import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';

import Checkbox from '@material-ui/core/Checkbox';

import EditIcon from '@material-ui/icons/Edit';
import IconButton from "@material-ui/core/IconButton";

import moment from 'moment';

// TODO move to common utils
function formatDate(date) {
  return moment(date).format('MM/DD/YYYY');;
}

function getStatusCSS(status) {

  if(status === "Complete") {
    return {
      textDecoration: "line-through"
    }
  }
  else {
    return { };
  }
}

const EventTable = ({events,
  dateColumnName,
  taskColumnName,
  onEdit,
  selectedRows,
  onSelect,
  onSelectAll
}) => (
  <Table>
    <TableHead>
      <TableRow>
        <TableCell padding="checkbox">
          <Checkbox
            checked={(selectedRows.size === events.length)}
            onChange={(event) => onSelectAll(event.target.checked)}
          />
        </TableCell>
        <TableCell>Status</TableCell>
        <TableCell>{taskColumnName}</TableCell>
        <TableCell>{dateColumnName}</TableCell>
        <TableCell />
      </TableRow>
    </TableHead>
    <TableBody>
      {events.map(row => {
        return (
          <TableRow key={row.id} style={getStatusCSS(row.status)}>
            <TableCell padding="checkbox">
              <Checkbox
                checked={selectedRows.has(row.id)}
                onChange={(event) => onSelect(row.id, event.target.checked)}
              />
            </TableCell>
            <TableCell>{row.status}</TableCell>
            <TableCell>{row.task}</TableCell>
            <TableCell component="th" scope="row">
              {formatDate(row.dueDate)}
            </TableCell>

            <TableCell  padding="checkbox">
              <IconButton onClick={() => onEdit(row)}>
                <EditIcon />
              </IconButton>
            </TableCell>

          </TableRow>
        );
      })}
    </TableBody>
  </Table>
);

class DatedList extends Component
{

  render() {
    const {
      dateColumnName,
      taskColumnName,
      events,
      onEdit,
      selectedRows,
      onSelect,
      onSelectAll
    } = this.props;

    return(
      <div>
        <EventTable events={events}
          dateColumnName={dateColumnName}
          taskColumnName = {taskColumnName}
          onEdit={onEdit}
          selectedRows={selectedRows}
          onSelect={onSelect}
          onSelectAll={onSelectAll}
        />
      </div>
    );
  }
}

export default DatedList;


