import React, { Component } from 'react';

import TextField from '@material-ui/core/TextField';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import Button from '@material-ui/core/Button';

const fieldStyle = {
  marginBottom: 10
}

const defaultValues = {
  category: "as",
  type:"a",
  seeded:"a",
  planted:"a",
  harvestStart:"a",
  harvestEnd:"a"
}

class SetupEditor extends Component {


  static getDerivedStateFromProps(nextProps, prevState){

    if(nextProps.open && !prevState.isOpen) {
      return {
        isOpen: true,
        edit: nextProps.edit
      }
    }
    else if(!nextProps.open && prevState.isOpen) {
      return {
        isOpen: false,
      }
    }
    else {
      return null;
    }
 }

  constructor(props) {
    super(props);
    this.state = {
      isOpen: false,
      edit: defaultValues
    }
  }

  handleEdit = field => event => {
    const obj = this.state.edit;
    obj[field] = event.target.value;
    this.setState({edit: obj});
  }

  render() {
    const { category, name, seeded, planted, harvestStarts, harvestEnds } = this.state.edit;
    console.log(seeded);
    return(
      <Dialog
        open={this.state.isOpen}
        onClose={this.handleClose}
        aria-labelledby="form-dialog-title"
      >
        <DialogTitle id="form-dialog-title">Seed Editor</DialogTitle>
        <DialogContent>
        <div>
          <TextField
            autoFocus
            margin="dense"
            id="category"
            label="Category"
            fullWidth
            style={fieldStyle}
            onChange={this.handleEdit("category")}
            value={category}
          />
          
          <TextField
            margin="dense"
            id="name"
            label="Name"
            fullWidth
            style={fieldStyle}
            onChange={this.handleEdit("name")}
            value={name}
          />
          <TextField
            id="seeded"
            label="Date Seeded"
            type="date"
            fullWidth
            InputLabelProps={{
              shrink: true,
            }}
            style={fieldStyle}
            onChange={this.handleEdit("seeded")}
            value={seeded}
          />
          <TextField
            margin="dense"
            id="planted"
            label="Date Planted"
            type="date"
            fullWidth
            InputLabelProps={{
              shrink: true,
            }}
            style={fieldStyle}
            onChange={this.handleEdit("planted")}
            value={planted}
          />
            <div style={{display:"flex",justifyContent:"center", alignItems:"center", width:"100%"}}>
 
              <TextField
                margin="dense"
                id="harvestStarts"
                label="Harvest Start"
                type="date"
                
                InputLabelProps={{
                  shrink: true,
                }}
                style={{width:"48%", marginRight:"2%"}}
                onChange={this.handleEdit("harvestStarts")}
                value={harvestStarts}
              />
 
              <TextField
                margin="dense"
                id="harvestEnds"
                label="Harvest End"
                type="date"
                
                InputLabelProps={{
                  shrink: true,
                }}
                style={{width:"48%", marginLeft:"2%"}}
                onChange={this.handleEdit("harvestEnds")}
                value={harvestEnds}
              />
            </div>
          </div>
        </DialogContent>
        <DialogActions>
          <Button onClick={this.props.onCancel} color="primary">
            Cancel
          </Button>
          <Button onClick={() => this.props.onSave(this.state.edit)} color="primary">
            Save
          </Button>
        </DialogActions>
      </Dialog>
    );
  }
}

export default SetupEditor;


