import React, { Component } from 'react';
import ExpansionPanel from '@material-ui/core/ExpansionPanel';
import ExpansionPanelSummary from '@material-ui/core/ExpansionPanelSummary';
import ExpansionPanelDetails from '@material-ui/core/ExpansionPanelDetails';
import Typography from '@material-ui/core/Typography';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import ExpandLessIcon from '@material-ui/icons/ExpandLess';
import EditIcon from '@material-ui/icons/Edit';
import DeleteIcon from '@material-ui/icons/Delete';

import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Button from '@material-ui/core/Button';
import AddIcon from '@material-ui/icons/Add';
import Toolbar from '@material-ui/core/Toolbar';
import Paper from "@material-ui/core/Paper"
import IconButton from "@material-ui/core/IconButton";

import moment from 'moment';

import "./SetupEdit";
import SetupEditor from './SetupEdit';

import { api } from "../api/dataapi";

const viewStyle = {
  width: "85%"
}



const defaultEntryValues = {
  category: "",
  name:"",
  seeded:"",
  planted:"",
  harvestStarts:"",
  harvestEnds:""
}

function formatDate(date) {
  return moment(date).format('MM/DD/YYYY');;
}

function formatDateRange(start, end) {
  if(end !== undefined && end !== null && end !== "") {
    return formatDate(start) + " - " + formatDate(end);
  }
  return formatDate(start);
}

const PlantTable = ({plants, onEdit, onDelete}) => (
  <Table>
    <TableHead>
      <TableRow>
        <TableCell>Name</TableCell>
        <TableCell>Seed</TableCell>
        <TableCell>First Planting</TableCell>
        <TableCell>Harvest</TableCell>
        <TableCell  padding="checkbox"></TableCell>
        <TableCell  padding="checkbox"></TableCell>
      </TableRow>
    </TableHead>
    <TableBody>
      {plants.map(row => {
        return (
          <TableRow key={row.id}>
            <TableCell component="th" scope="row">
              {row.name}
            </TableCell>
            <TableCell>{formatDate(row.seeded)}</TableCell>
            <TableCell>{formatDate(row.planted)}</TableCell>
            <TableCell>{formatDateRange(row.harvestStarts, row.harvestEnds)}</TableCell>
            <TableCell  padding="checkbox">
              <IconButton onClick={() => onEdit(row)}>
                <EditIcon />
              </IconButton>
            </TableCell>
            <TableCell  padding="checkbox">
            <IconButton onClick={() => onDelete(row)}>
              <DeleteIcon />
            </IconButton>
            </TableCell>
          </TableRow>
        );
      })}
    </TableBody>
  </Table>
);

const CategoryPanel = ({crop, expanded, onExpand, onEdit, onDelete}) => (
  <ExpansionPanel expanded={expanded} onChange={onExpand}>
    <ExpansionPanelSummary expandIcon={<ExpandMoreIcon />}>
      <Typography>{crop.category}</Typography>
    </ExpansionPanelSummary>
    <ExpansionPanelDetails>
      <PlantTable plants={crop.seeds} onEdit={onEdit} onDelete={onDelete} />
    </ExpansionPanelDetails>
  </ExpansionPanel>
);

class Plants extends Component {


  constructor(props) {
    super(props);

    this.state = {
      categoryData: [],
      expanded: new Set(),
      editOpen: false,
      editObject: defaultEntryValues
    };
  }

  handleExpand = crop => (event, expanded) => {
    const current = this.state.expanded;
    if(expanded) {
      current.add(crop);
    }
    else {
      current.delete(crop);
    }
    this.setState({expanded:current});
  }

  handleExpandAll = () => {
    const updatedExpand = new Set();
    this.state.categoryData.forEach(entry => {
      updatedExpand.add(entry.category);
    });
    this.setState({expanded:updatedExpand});
  }

  handleExpandNone = () => {
    this.setState({expanded: new Set()});
  }

  handleAdd = () => {
      
    const editObject = {
      id: -1,
      category: defaultEntryValues.category,
      name: defaultEntryValues.name,
      seeded: defaultEntryValues.seeded,
      planted: defaultEntryValues.planted,
      harvestStarts: defaultEntryValues.harvestStarts,
      harvestEnds: defaultEntryValues.harvestEnds
    }

    this.setState({editOpen:true, editObject });
  }

  handleEdit = (row) => {
    const editObject = {
      id: row.id,
      category: row.category,
      name: row.name,
      seeded: row.seeded,
      planted: row.planted,
      harvestStarts: row.harvestStarts,
      harvestEnds: row.harvestEnds
    }
    this.setState({editOpen:true, editObject });
  }

  handleDelete = (row) => {
    api.deleteSeed(row).then(() => {
      this.loadAllData();
    });
  }

  handleSave = (item) => {
    const expanded = this.state.expanded;
    expanded.add(item.category);
    this.setState({editOpen:false, expanded});
    if(item.id === -1) {
      api.addSeed(item).then(() => {
        this.loadAllData();
      });
    }
    else {
      api.editSeed(item).then(() => {
        this.loadAllData();
      });
    }
  }

  handleCancel = () => {
    console.log("closing");
    this.setState({editOpen:false, editObject: defaultEntryValues});
  }

  loadAllData = () => {
    api.getAllCrops().then(crops => {
      const cats = [];
      if(crops.length === 0) {
        this.setState({categoryData: cats});
        return;
      }

      
      let currentCategory = null;
      console.log(crops);
      crops.forEach(crop => {
        const seed = {
          id: crop.id,
          category: crop.category,
          name: crop.name,
          seeded: crop.seeded,
          planted: crop.planted,
          harvestStarts: crop.harvestStarts,
          harvestEnds: crop.harvestEnds
        }

        if(currentCategory === null) {
          currentCategory = {
            category: crop.category,
            seeds: [seed]
          };
        }
        else if(currentCategory.category === crop.category) {
          currentCategory.seeds.push(seed);
        }
        else {
          cats.push(currentCategory);
          currentCategory = {
            category: crop.category,
            seeds: [seed]
          };
        }
      });
      cats.push(currentCategory);
      console.log(cats);
      this.setState({categoryData: cats});
    });
  }

  componentDidMount() {
    this.loadAllData();
  }

  render() {

    const { categoryData, expanded } = this.state;

    const panels = [];
    console.log(categoryData);
    categoryData.forEach(entry => {
      const name = entry.category;
      panels.push(
        <CategoryPanel key={name} 
          crop={entry} 
          expanded={expanded.has(name)} 
          onExpand={this.handleExpand(name)}
          onEdit={this.handleEdit}
          onDelete={this.handleDelete}
        />
      );
    });

    return (
      <div style={viewStyle}>

      <SetupEditor
        open={this.state.editOpen}
        onSave={this.handleSave}
        onCancel={this.handleCancel}
        edit={this.state.editObject}
      />

      <Paper>
        <Toolbar>
          <Typography variant="title" color="inherit" style={{flexGrow:1}}>
            Garden Setup
          </Typography>
          <IconButton onClick={this.handleAdd}>
            <AddIcon />
          </IconButton>
          <IconButton onClick={this.handleExpandAll}>
            <ExpandMoreIcon />
          </IconButton>
          <IconButton onClick={this.handleExpandNone}>
            <ExpandLessIcon />
          </IconButton>
        </Toolbar>
      </Paper>
        {panels}
      </div>
    );
  }
}

export default Plants;


