import React, { Component } from 'react';

import DatedList from "../components/DatedList"

import Button from '@material-ui/core/Button';
import AddIcon from '@material-ui/icons/Add';
import DeleteIcon from '@material-ui/icons/Delete';
import Toolbar from '@material-ui/core/Toolbar';
import Paper from "@material-ui/core/Paper"
import IconButton from "@material-ui/core/IconButton";

import Typography from '@material-ui/core/Typography';

import TaskEditor from "./TaskEditor";
import { api } from "../api/dataapi";

const defaultValues = {
  id: -1,
  dueDate: "",
  task:"",
  status: "Not Started",
}

class WorkLog extends Component
{

  constructor(props) {
    super(props);
    this.state = {
      showEdit: false,
      toEdit: defaultValues,
      events: [],
      selectedRows: new Set(),
    }
  }

  handleAdd = () => {
    this.showEditDialog(defaultValues);
  }

  handleEdit = (row) => {
    console.log(row);
    this.showEditDialog(row);
  }

  showEditDialog = (toEdit) => {
    const editedValues = {
      id: toEdit.id,
      dueDate: toEdit.dueDate,
      task: toEdit.task,
      status: toEdit.status,
    }
    this.setState({showEdit: true, toEdit: editedValues});
  }

  handleSave = (entry) => {
    if(entry.id === -1) {
      api.addWorkEvent(entry).then(() => {
        this.setState({showEdit: false});
        this.getAllEvents();
      });
    }
    else {
      api.editWorkEvent(entry).then(() => {
        this.setState({showEdit: false});
        this.getAllEvents();
      });
    }
  }

  handleCancel = () => {
    this.setState({showEdit: false});
  }

  getAllEvents = () => {  
    api.getWorkLogEvents().then(results => {
      this.setState({events: results});
    })
  }

  handleSelect = (rowId, isSelected) => {
    const selectedRows = this.state.selectedRows;
    
    if(isSelected) {
      selectedRows.add(rowId);
    }
    else {
      selectedRows.delete(rowId);
    }
    this.setState({selectedRows});
  }

  handleSelectAll = (selectAll) => {
    const selectedRows = new Set();
    if(selectAll) {
      this.state.events.forEach(event => {
        selectedRows.add(event.id);
      });
    }
    this.setState({selectedRows});
  }

  componentWillMount() {
    this.getAllEvents();
  }

  handleDelete = () => {
    api.removeEvents(this.state.selectedRows).then(() => {
      this.getAllEvents();
    });
  }

  render() {
    return (
      <div>
        
        <TaskEditor 
          open={this.state.showEdit}
          onSave={this.handleSave}
          onCancel={this.handleCancel}
          edit={this.state.toEdit}
        />
        
        <Paper>
        <Toolbar>
          <Typography variant="title" color="inherit" style={{flexGrow:1}}>
            Work Log
          </Typography>
          <IconButton onClick={this.handleAdd}>
            <AddIcon />
          </IconButton>
          <IconButton onClick={this.handleDelete}>
            <DeleteIcon />
          </IconButton>
        </Toolbar>
        </Paper>
        <DatedList events={this.state.events}
          dateColumnName="Due Date"
          taskColumnName="Task"
          onEdit={this.handleEdit}
          onSelect={this.handleSelect}
          selectedRows={this.state.selectedRows}
          onSelectAll={this.handleSelectAll}
        />
      </div>
    );
  }
}

export default WorkLog;


