import React, { Component } from 'react';

import PlantProgress from "../components/PlantProgress";
import Typography from '@material-ui/core/Typography';
import Toolbar from '@material-ui/core/Toolbar';
import Paper from '@material-ui/core/Paper';

import { api } from "../api/dataapi";

class GardenView extends Component
{
  state = {
    data: []
  }


  getData = () => {
    api.getAllCrops().then((data) => {
      this.setState({data})
    });
  }

  componentWillMount() {
    this.getData();
  }

  render() {

    const bars = [];
    this.state.data.forEach(plant => {
      bars.push(
        <PlantProgress key={plant.id} plant={plant} />
      )
    })

    return (
      <div>
        {bars}
      </div>
    );
  }
}

export default GardenView;


