import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Stepper from '@material-ui/core/Stepper';
import Step from '@material-ui/core/Step';
import StepLabel from '@material-ui/core/StepLabel';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';

import moment from 'moment';

const styles = theme => ({
  root: {
    width: '90%',
  },
  backButton: {
    marginRight: theme.spacing.unit,
  },
  instructions: {
    marginTop: theme.spacing.unit,
    marginBottom: theme.spacing.unit,
  },
});


const test = {
  id:1,
  category: "Leaf",
  name: "Kale",
  seeded: "2019-01-01",
  planted:"2019-01-15",
  harvestStarts: "2019-02-15",
  harvestEnds: "2019-02-26"
}

// TODO move to common utils
function formatDate(date) {
  return moment(date).format('MM/DD/YYYY');;
}

class PlantProgress extends React.Component {
  state = {
    activeStep: 0,
  };

  getSteps(plant) {
    const steps = [];
    // const {seeded, planted, harvestStarts, harvestEnds} = plant;
    // if(seeded !== undefined && seeded !== "") {
    //   steps.push("Seeding");
    // }

    // if(planted !== undefined && planted !== "") {
    //   steps.push("First Planting");
    // }

    // if(harvestStarts !== undefined && harvestStarts !== "") {

    //   if(harvestEnds !== undefined && harvestEnds !== "") {
    //     steps.push("Harvest Begins");
    //     steps.push("Harvest Ends'");
    //   }
    //   else {
    //     steps.push("Harvest");
    //   }
      
    // }
    return [
      {
        label: "Seeding",
        date: formatDate(plant.seeded)
      },
      {
        label: "First Planting",
        date: formatDate(plant.planted)
      },
      {
        label: "Harvest Begins",
        date: formatDate(plant.harvestStarts)
      },
      {
        label: "Harvest Ends",
        date: plant.harvestEnds !== "" ? formatDate(plant.harvestEnds) : 
        formatDate(plant.harvestStarts)
      } 
    ];
  }

  getActiveStep(plant) {
    const {seeded, planted, harvestStarts, harvestEnds} = plant;
    const current = moment();
    if(harvestEnds !== "" && current.isAfter(harvestEnds)) {
      return 3;
    }

    if(harvestEnds === "" && current.isAfter(harvestStarts)) {
      // then we are complete!
      return 3;
    }

    else if(current.isAfter(harvestStarts)) {
      return 2;
    }
    else if(current.isAfter(planted)) {
      return 1;
    }
    else if(current.isAfter(seeded)) {
      return 0;
    }
    else {
      return -1;
    }

  }

  render() {
    const { classes, plant } = this.props;
    const steps = this.getSteps(plant);
    const activeStep  = this.getActiveStep(plant);

    return (
      <div className={classes.root}>
        <Typography variant="subheading">{plant.name}</Typography>
        <Stepper activeStep={activeStep} >
          {steps.map(label => {
            const labelProps = {
              optional: <Typography variant="caption">{label.date}</Typography>
            }
            return (
              <Step key={label.label}>
                <StepLabel {...labelProps}>{label.label}</StepLabel>
              </Step>
            );
          })}
        </Stepper>
      </div>
    );
  }
}

PlantProgress.propTypes = {
  classes: PropTypes.object,
};

export default withStyles(styles)(PlantProgress);


