import React, { Component } from 'react';

import TextField from '@material-ui/core/TextField';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import Button from '@material-ui/core/Button';

import Input from '@material-ui/core/Input';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import FormHelperText from '@material-ui/core/FormHelperText';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';

const fieldStyle = {
  marginBottom: 10
}

const defaultValues = {
  id: -1,
  dueDate: "",
  task:"",
  status: "Not Started",
}

class TaskEditor extends Component {


  static getDerivedStateFromProps(nextProps, prevState){

    if(nextProps.open && !prevState.isOpen) {
      return {
        isOpen: true,
        edit: nextProps.edit
      }
    }
    else if(!nextProps.open && prevState.isOpen) {
      return {
        isOpen: false,
      }
    }
    else {
      return null;
    }
 }

  constructor(props) {
    super(props);
    this.state = {
      isOpen: false,
      edit: defaultValues
    }
  }

  handleEdit = field => event => {
    const obj = this.state.edit;
    obj[field] = event.target.value;
    this.setState({edit: obj});
  }

  render() {
    const { task, dueDate, status } = this.state.edit;
    return(
      <Dialog
        open={this.state.isOpen}
        onClose={this.handleClose}
        aria-labelledby="form-dialog-title"
      >
        <DialogTitle id="form-dialog-title">Task Editor</DialogTitle>
        <DialogContent>
        <div>
          <TextField
            autoFocus
            margin="dense"
            id="task"
            label="Task"
            fullWidth
            style={fieldStyle}
            onChange={this.handleEdit("task")}
            value={task}
          />

          <TextField
            
            id="dueDate"
            label="Due Date"
            type="date"
            fullWidth
            InputLabelProps={{
              shrink: true,
            }}
            style={fieldStyle}
            onChange={this.handleEdit("dueDate")}
            value={dueDate}
          />
          
        <FormControl fullWidth>
          <InputLabel htmlFor="task-status">Status</InputLabel>
          <Select
            value={status}
            onChange={this.handleEdit("status")}
            input={<Input name="status" id="task-status" />}
          >
            <MenuItem value={"Not Started"}>Not Started</MenuItem>
            <MenuItem value={"In Progress"}>In Progress</MenuItem>
            <MenuItem value={"Complete"}>Complete</MenuItem>
          </Select>
        </FormControl>


          </div>
        </DialogContent>
        <DialogActions>
          <Button onClick={this.props.onCancel} color="primary">
            Cancel
          </Button>
          <Button onClick={() => this.props.onSave(this.state.edit)} color="primary">
            Save
          </Button>
        </DialogActions>
      </Dialog>
    );
  }
}

export default TaskEditor;


